import React from "react";
import Link from "next/link";

export default function ConcertsSection() {
  const upcomingConcerts: Array<{
    date: string;
    venue: string;
    city: string;
    info?: string;
  }> = []; // Vide pour l'instant comme dans la méthode de travail

  const pastConcerts = [
    {
      date: "20 octobre 2024",
      venue: "Jazz Club",
      city: "Paris",
    },
    {
      date: "15 septembre 2024",
      venue: "Blue Note",
      city: "Lyon",
    },
  ];

  return (
    <section className="py-16 px-4 bg-[#0f1419]">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-4xl font-bold text-[#f0f0f0] mb-8 text-center">
          UPCOMING SHOWS
        </h2>

        {upcomingConcerts.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-[#c0c0c0] text-lg mb-4">
              Aucun concert prévu pour le moment.
            </p>
            <p className="text-[#808080] text-sm">
              Restez connecté pour connaître les prochaines dates !
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {upcomingConcerts.map((concert, index) => (
              <div
                key={index}
                className="bg-[#1a1f3a] p-6 rounded-lg border border-[#2a2f4a]"
              >
                <p className="text-[#d4af37] font-semibold mb-2">
                  {concert.date}
                </p>
                <h3 className="text-xl font-bold text-[#f0f0f0] mb-1">
                  {concert.venue}
                </h3>
                <p className="text-[#c0c0c0]">{concert.city}</p>
                {concert.info && (
                  <p className="text-[#808080] text-sm mt-2">{concert.info}</p>
                )}
              </div>
            ))}
          </div>
        )}

        {pastConcerts.length > 0 && (
          <div className="mt-16">
            <h3 className="text-2xl font-bold text-[#f0f0f0] mb-6 text-center">
              Dernières dates
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {pastConcerts.map((concert, index) => (
                <div
                  key={index}
                  className="bg-[#1a1f3a] p-4 rounded-lg border border-[#2a2f4a] opacity-75"
                >
                  <p className="text-[#808080] text-sm mb-1">{concert.date}</p>
                  <p className="text-[#c0c0c0] font-medium">
                    {concert.venue} - {concert.city}
                  </p>
                </div>
              ))}
            </div>
          </div>
        )}

        <div className="text-center mt-8">
          <Link
            href="/concerts"
            className="inline-block px-6 py-3 bg-[#d4af37] text-[#0a0e1a] font-semibold rounded-lg hover:bg-[#c49b2a] transition-colors"
          >
            Voir tous les concerts
          </Link>
        </div>
      </div>
    </section>
  );
}

